import NcSelect from '@nextcloud/vue/dist/Components/NcSelect';
export default {
  name: 'OIDCItem',
  components: {
    NcSelect: NcSelect
  },
  props: {
    client: {
      type: Object,
      required: true
    },
    groups: {
      type: Array,
      required: true
    }
  },
  data: function data() {
    return {
      id: this.client.id,
      name: this.client.name,
      redirectUris: this.client.redirectUris,
      clientId: this.client.clientId,
      clientSecret: this.client.clientSecret,
      signingAlg: this.client.signingAlg,
      type: this.client.type,
      renderSecret: false,
      addRedirectUri: '',
      groupData: {
        props: {
          inputId: this.client.id + '-group-select',
          multiple: true,
          closeOnSelect: true,
          options: this.groups,
          value: this.client.groups
        }
      }
    };
  },
  computed: {
    renderedSecret: function renderedSecret() {
      if (this.renderSecret) {
        return this.clientSecret;
      } else {
        return '****';
      }
    },
    isPublic: function isPublic() {
      if (this.type === 'public') return true;
      return false;
    }
  },
  methods: {
    toggleSecret: function toggleSecret() {
      this.renderSecret = !this.renderSecret;
    },
    addRedirect: function addRedirect() {
      this.$emit('addredirect', this.id, this.addRedirectUri);
      this.addRedirectUri = '';
    },
    updateGroups: function updateGroups() {
      this.$emit('updategroups', this.id, this.groupData.props.value);
    }
  }
};